.row {
    /* margin-left: 1.25em; */
    /* margin-right: -0.75em; */
    max-height: 40rem;
    overflow-y: scroll;

}

.row::-webkit-scrollbar {
    width: 1px;
  }

.row::after {
    content: " ";
    display: block;
    clear: both;
}

/* .column50 {
    width: 100%;
} */
[class*="column50"] {
    float: left;
    padding: -1em 0.75em;
    margin-top: 0;
    margin-left: 1.2em;
    /* width: min-content; */
}
.media {
    float: left;
    font-size: 0.5em;
    margin: 1.5em 1.5em 0.5em 0;
    width: calc(50% - 1.5em);
    height: auto;
   padding: 2.4em; 
   background-color: #eeeeee7a;
   border-radius: 0.5em;
   box-shadow: 0 8px 15px 0 rgba(0,0,0,0.3), 0 5px 21px 0 rgba(0,0,0,0.18);
}
.media p {
  font-family: 'Pompadur';
    text-align: center;
    font-size: 1.7em;
    color: var(--colorText);
    /* background-color: #b3b3b3; */
    /* border-radius: 0.5em; */
    border-top-left-radius: 0em;
    border-top-right-radius: 0em;
    border-bottom-right-radius: 0.5em;
    border-bottom-left-radius: 0.5em;
    margin: 0%;
    /* padding: 0 1em 0 1em; */
}
.media span {
  font-family: 'Pompadur';
    text-align: center;
    font-size: 1.7em;
    background-color: #688f6d;
    /* border-radius: 0.5em; */
    border-top-left-radius: 0em;
    border-top-right-radius: 0em;
    border-bottom-right-radius: 0.5em;
    border-bottom-left-radius: 0.5em;
    margin: 0%;
    color: #eff1e2;
    /* padding: 0 1em 0 1em; */
}
.media:hover{
   cursor: pointer;
