  .mediaImage_modal {
    max-width: none;
    min-width: auto;
    min-height: auto;
    padding: 1.5rem;
    height: 60%;
    width: auto;
  }
  
  .mediaImage_modal >img {
    /* display: block;
    width: 100%;
    height: 87px;
    object-fit: cover; */
    display: block;
    width: 121%;
    height: 108px;
    object-fit: cover;
    border-radius: 50%;
    box-shadow: var(--boxShadow);
  }
  
  .headerSong {
    text-align: right;
    font-size: 1rem;
    /* padding: 0.2rem 1.3rem; */
    color: var(--titleSongColor);
    /* background-color: var(--colorBackgroundAll); */
    border-radius: 0.5rem;
    /* border: 2px solid var(--titleSongColor); */
    /* border-bottom: 4px solid #b2b1b1; */
    /* text-shadow: -1px -1.6px 0px #151616; */
