    cursor: pointer;
    padding: 0 1em 0 1em;
  }
  .headerSong h2 p {
    margin: 0%;
    padding: 0 1em 0 1em;
  }
  
  .linkTo {
    position: absolute;
    top: 97%;
    left: 31%;
    background-color: rgb(211 24 24);
    border: none;
    color: white;
    padding: 2px 6px;
    font-size: 14px;
    cursor: pointer;
    border-radius: .5em;
    z-index: 7;
  }
  
  .audioBlock {
    display: block;
  }
  
  .audioBlock p {
    font-size: 1.5rem;
    text-align: center;
    color: var(--colorText);
    /* padding: 2px 6px; */
