    /* margin: 5em; */
    /* background-color: var(--tributeAfterBefore); */
    border-radius: 0.5em;
    /* text-shadow: var(--textShadow); */
    /* box-shadow: var(--boxShadow); */
  }
  
  .videoBlock p {
    display: block;
    font-size: 1.5rem;
    text-align: center;
    color: var(--colorText);
    /* background-color: var(--tributeAfterBefore); */
    border-radius: 0.5em;
    text-shadow: var(--textShadow);
    /* box-shadow: var(--boxShadow); */
  }
  
  .videoBlock {
    position: relative;
  
    width: 100%;
    /* height: 255px; */
  
  }
  
  #videoFrame {
    position: relative;
    
    width: 100% !important;
    height: 22vh !important;
    border-width: 0;
    outline-width: 0;
  }
  
  audio::-webkit-media-controls-enclosure {
  
    background-color: #a09f9b;
    text-shadow: -1px -1.6px 0px #e4e0e7;
    box-shadow: var(--boxShadow);
  
  }
  

  
